/** @format */

section.AccountDetail {
  width: 100%;
  height: 100vh;
  font-family: Roboto;
  margin-bottom: 30px;
}

.Form_Header {
  padding-top: 32px;
  display: grid;
  grid-template-columns: 15% 80%;
  margin-bottom: 20px;
}

.Form_Header .Back_Btn {
  align-self: center;
  text-align: center;
}

.Header_Text h1 {
  text-align: start;
  font-size: 24px;
  font-weight: 500;

  line-height: 1.17;
  letter-spacing: normal;
  color: #3b414a;
}
